import { Image , Divider, FloatButton, Timeline} from 'antd';
import { HomeOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';

function Events(){
    const navigate = useNavigate();

    return (
        <center>
        <div 
                style={
                    {
                        backgroundColor: "#98824f",
                        height: "11vh",
                        borderBottomLeftRadius: "30px",
                        borderBottomRightRadius: "30px",
                        maxWidth: "1080px",
                        marginBottom:"5vh"
                    }
                }>
                     <FloatButton
                        onClick={()=>{ navigate("/")}}
                        style={{ width: "75px", height: "75px" }}
                        tooltip={<div>Go Back to Home</div>}
                        icon={<HomeOutlined />}
                    />
                    <center>
                        <Image
                            style={{
                                borderRadius: "20px",
                                marginTop: "5vh",
                                width:"90vw",
                                maxWidth:"1000px"
                            

                            }}
                            maxWidth={1000}
                            src="images/AppBanner.jpg"
                        />

                        <div
                         style={{
                            borderRadius: "20px",
                            marginTop: "3vh",
                            width:"90vw",
                            maxWidth:"1000px"
                        }}
                        >
                            <Divider style={{marginTop: "5vh", marginBottom: "5vh"}} orientation="left">Day Events / Activities</Divider>

                            <Timeline
                                style={{marginLeft:"-43vw", width: '96vw'}}
                                mode={"left"}
                                items={[
                                {
                                    label: 'Day 1',
                                    children: (
                                        <div style={{marginLeft:"-75px"}}>
                                          <b>23 October<br/></b>
                                          <small>Golf<br/></small>
                                          <small>Crocodile Tour<br/></small>
                                          <small>Unguided Falls Tour<br/></small>
                                          <small><i>To register and self funding</i><br/></small>
                                        </div>
                                      ),
                                },
                                {
                                    label: 'Day 3',
                                    children: (
                                        <div style={{marginLeft:"-75px"}}>
                                          <b>25 October 2024 | 10:00 - 16:00<br/></b>
                                          <small>Complimentary Activities<br/></small>
                                          <small>Theme: Neo Retro 80s/90s classics<br/></small>
                                          <small>Game drive / Boat Cruise<br/></small>
                                        </div>
                                      ),
                                },
                                
                                ]}
                            />
                          
                        </div>
                    
                    </center>

                    
        
        </div>
       
       </center>
        
    );
}

export default Events;