import { Image , Divider, FloatButton, Timeline} from 'antd';
import { HomeOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';

function Program(){
    const navigate = useNavigate();

    return (
        <center>
        <div 
                style={
                    {
                        backgroundColor: "#98824f",
                        height: "11vh",
                        borderBottomLeftRadius: "30px",
                        borderBottomRightRadius: "30px",
                        maxWidth: "1080px",
                        marginBottom:"5vh"
                    }
                }>
                     <FloatButton
                        onClick={()=>{ navigate("/")}}
                        style={{ width: "75px", height: "75px" }}
                        tooltip={<div>Go Back to Home</div>}
                        icon={<HomeOutlined />}
                    />
                    <center>
                        <Image
                            style={{
                                borderRadius: "20px",
                                marginTop: "5vh",
                                width:"90vw",
                                maxWidth:"1000px"
                            

                            }}
                            maxWidth={1000}
                            src="images/AppBanner.jpg"
                        />

                        <div
                         style={{
                            borderRadius: "20px",
                            marginTop: "3vh",
                            width:"90vw",
                            maxWidth:"1000px"
                        }}
                        >
                            <Divider style={{marginTop: "5vh", marginBottom: "5vh"}} orientation="left">Program</Divider>

                            <Timeline
                                style={{marginLeft:"-43vw", width: '96vw'}}
                                mode={"left"}
                                items={[
                                {
                                    label: '23 October',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                        <b>Session 1<br/></b>
                                        <small>Opening remarks and introduction<br/></small>
                                        <small>Presentation 1: Distruptive Innovation in Advertising, Redefining the ad landscape - <i>Paul Makaya</i><br/></small>
                                        <small>Presentation 2: Distruptive Technologies and the future of ecommerce - <i>Vinod Sharma</i><br/></small>
                                        
                                        </div>
                                    ),
                                }, {
                                    label: '24 October',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                        <b>Session 2:  08:00 - 09:00<br/></b>
                                        <small>Leveraging Generative AI for next-generation Business Intelligence<br/></small>
                                        <small><i>Abraham Makamba</i><br/></small>
                                        </div>
                                    ),
                                }, {
                                    label: '09:00 - 10:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                        <b>Session 3<br/></b>
                                        <small>Global Trends in Distruptive Marketing: Lessons from leading Markets<br/></small>
                                        <small><i>Dennis Mambure</i><br/></small>
                                        </div>
                                    ),
                                },
                                {
                                    label: '10:00 - 10:15',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                          <small>Tea Break<br/></small>
                                         
                                        </div>
                                      ),
                                },
                                {
                                    label: '10:15  - 11:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                          <b>Session 4<br/></b>
                                          <small>Official opening of the conference<br/></small>
                                          <small><i>Hon. Minister Dr. Tatenda Mavetera</i><br/></small>
                                        </div>
                                      ),
                                },
                                {
                                    label: '11:00 - 12:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                          <b>Session 5<br/></b>
                                          <small>The revolving role of the CMO: From Marketing to business Leadership<br/></small>
                                          <small><i>Mabel Mun'gomba</i></small>
                                        </div>
                                      ),
                                }, {
                                    label: '12:00 - 13:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                          <b>Session 6<br/></b>
                                          <small>Ethical AI and responsive Data usage<br/></small>
                                          <small><i>Eng. Kudzai C. Chikumbirike</i></small>
                                        </div>
                                      ),
                                }, {
                                    label: '13:00 - 14:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                          <small>Lunch Break<br/></small>
                                         
                                        </div>
                                      ),
                                },{
                                    label: '14:00 - 15:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                           <b>Panel Session<br/></b>
                                          <small>Marketing and sales in Vuca Environmement: A look into the Zim Context<br/></small>
                                          <small>Facilitators: <i>Andrew Tawodzera, Maureen Chirambaguhwa, Irimayi Muzorehwa</i></small>
                                         
                                        </div>
                                      ),
                                },{
                                    label: '15:00 - 16:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                           <b>Panel Session<br/></b>
                                          <small>Sustainable Branding: Marketing's role in the ESG Revolution<br/></small>
                                          <small><i>Walter Mwasa, Lillian Mbayiwa</i></small>
                                         
                                        </div>
                                      ),
                                },{
                                    label: '07:00 - 08:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                          <small>Breakfast<br/></small>
                                         
                                        </div>
                                      ),
                                },{
                                    label: '08:00 - 09:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                          <small>Driving business model change: The marketing advantage<br/></small>
                                          <small><i>Unaiswi Nyikadzino</i></small>
                                        </div>
                                      ),
                                },{
                                    label: '09:00 - 10:00',
                                    children: (
                                        <div style={{marginLeft:"-90px"}}>
                                            <b>Session 10<br/></b>
                                          <small>The rise of the Digital/Disruptive Business model<br/></small>
                                          <small><i>Agripa Mugwagwa</i></small>
                                        </div>
                                      ),
                                },
                                ]}
                            />
                          
                        </div>
                    
                    </center>

                    
        
        </div>
       
       </center>
        
    );
}

export default Program;